@import (less) "color.less";
.panel-default{
  border:none !important;

  &>.panel-heading {
    color: @lighter_green;
    text-decoration: none;
    background-color: transparent;
    border: none !important;
  }
  &>.panel-body-border {
    padding: 15px;
    border-top: 2px solid @light_gray;
    background-color: @lightest_gray;
  }
  &>.panel-body {
    padding: 15px;
    background-color: @lightest_gray;
  }
}